.App {
  margin: 0 auto;
  padding: 0;
  max-width: 768px;
}

* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0)
}

.container {
  margin: 0 auto;
  max-width: 768px;
}

body {
  background: #33CCCC; /* Fallback */
  animation: color 11s infinite linear;
  text-align: center;
}

.overlay {
  width: 100%;
  height: 1000px;
  z-index: 20;
  display: none;
  background-color: #000;
  position: absolute;
  opacity: 0.6;
  animation: overlayFadeOut;
  animation-duration: 0.5s;
}

.block {
  display: block;
  animation: overlayFadeIn;
  animation-duration: 0.5s;
}

@keyframes overlayFadeIn {
  from {
      transform: translate3d(0, 0px, 0);
      opacity: 0;
  }

  to {
      transform: translate3d(0, 0, 0);
      opacity: 0.6;
  }
}

@keyframes overlayFadeOut {
  from {
      transform: translate3d(0, 0, 0);
      opacity: 0.6;
      display: block;
  }

  to {
      transform: translate3d(0, 0, 0);
      opacity: 0;
  }
}

@keyframes startPage {
  from {
      transform: translate3d(0, 0px, 0);
      opacity: 0;
  }

  to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
  }
}

@keyframes color {
  0% {
      background: #33CCCC;
  }

  20% {
      background: #33CC36;
  }

  40% {
      background: #B8CC33;
  }

  60% {
      background: #FCCA00;
  }

  80% {
      background: #33CC36;
  }

  100% {
      background: #33CCCC;
  }
}